/**
 * _x, _y ê°’ì„ ì²´í¬í•˜ì—¬ ë¬¸ìžì—´ì¸ê²½ìš° ìœ íš¨í•œ ê°’ì¸ì§€, ìœ íš¨í•œ ê°’ì´ ì•„ë‹Œê²½ìš° px ê°’(Number)ìœ¼ë¡œ ë¦¬í„´.
 * @param {String|Number} _x left|right|center or pixel number
 * @param {String|Number} _y top|bottom|middle or pixel number
 * @returns {Object} x, y string|number
 */
function checkTargetPosXY(_x, _y){
  const xPosReg = /left|right|center/g;
  const yPosReg = /top|bottom|middle/g; 
  const x = (xPosReg.test(_x)) ? _x : parseInt(_x, 10);
  const y = (yPosReg.test(_y)) ? _y : parseInt(_y, 10);
  return {x, y};
}

/**
 * ë‚œìˆ˜ë¥¼ ë¦¬í„´í•˜ëŠ” í•¨ìˆ˜.
 * @param {number} len
 * @returns {Number} len ì˜ ê°’ì˜ ìžë¦¿ìˆ˜ë¥¼ ê°–ëŠ” ë‚œìˆ˜ë¥¼ ë¦¬í„´
 */
export function uid(len) {
  len = len || 7;
  return Math.random()
    .toString(35)
    .substr(2, len);
}

/**
 * esw ì œì–´ë¥¼ ìœ„í•œ í´ëž˜ìŠ¤
 * ê°ì‹œ ë˜ëŠ” ì—˜ë¦¬ë¨¼íŠ¸ ë³„ë¡œ í•´ë‹¹ í´ëž˜ìŠ¤ê°€ ìƒì„±ë¨.
 * @class
 */
class EswItem {
  /**
   * í´ëž˜ìŠ¤ ìƒì„±ì‹œ ê°ì‹œë˜ëŠ” element ì™€ ë©”ì¸ìœ¼ë¡œ ì‹¤í–‰ë˜ëŠ” í´ëž˜ìŠ¤ ìžì‹ ì„ ë°›ìŒ.
   * @constructor EswItem
   * @param {HTMLElement} element 
   * @param {ElementScrollWatcher} root 
   */
  constructor(element, root){
    this.id = uid(); // object ì²´í¬ë¥¼ ìœ„í•œ id ë‚œìˆ˜
    this.element = element;
    this.element.dataset.eswId = this.id; 

    // element ì˜ ê°œë³„ ì…‹íŒ… ê°’ì´ ìžˆëŠ”ì§€ ì²´í¬ í•˜ì—¬ ì €ìž¥
    this.datumPointX = (()=>{
      if( element.dataset.eswCheckX ) {
        return checkTargetPosXY(element.dataset.eswCheckX, 0).x;
      } else {
        return root.checkX;
      };
    })();

    // element ì˜ ê°œë³„ ì…‹íŒ… y ê°’ì´ ìžˆëŠ”ì§€ ì²´í¬ í•˜ê³  ì—†ìœ¼ë©´ ê¸°ë³¸ ì…‹íŒ…ê°’ì„ ì €ìž¥.
    this.datumPointY = (()=>{
      if( element.dataset.eswCheckY ) {
        return checkTargetPosXY(0, element.dataset.eswCheckY).y;
      } else {
        return root.checkY;
      };
    })();
    // element ì˜ ê°œë³„ ì…‹íŒ… delay ê°’ì´ ìžˆëŠ”ì§€ ì²´í¬ í•˜ê³  ì—†ìœ¼ë©´ ê¸°ë³¸ ì…‹íŒ…ê°’ì„ ì ìš©.
    this.activeTimer = (()=>{
      if( element.dataset.eswDelay ) {
        return parseInt(element.dataset.eswDelay, 10);
      } else {
        return root.option.activeDelay;
      }
    })();
    this._isIntersecting = false; // í™”ë©´ì— ë“¤ì–´ì™”ëŠ”ì§€ ìœ ë¬´
    this.timer = null; // íƒ€ì´ë¨¸ê°€ ì§€ì •ë¨
    this.activeFunction = root.option.active; // í™œì„±í™” ë˜ì—ˆì„ë•Œ ì‹¤í–‰ë  í•¨ìˆ˜
    this.deActiveFunction = root.option.deActive; // ë¹„í™œì„±í™” ë˜ì—ˆì„ë•Œ ì‹¤í–‰ë  í•¨ìˆ˜
    this.directFunction = root.option.direct; // ëŒ€ê¸°ì‹œê°„ ì—†ì´ ë°”ë¡œ ì‹¤í–‰ë  í•¨ìˆ˜
  }

  /**
   * í™”ë©´ì•ˆìœ¼ë¡œ ë“¤ì–´ì˜¤ê±°ë‚˜ ë‚˜ê°”ì„ë•Œ í•´ë‹¹ ê°’ì„ ì…‹íŒ… í•  ìˆ˜ ìžˆìŒ.
   * í•´ë‹¹ ê°’ì´ ë³€ê²½ë˜ë©´, íƒ€ì´ë¨¸ë¥¼ ì´ˆê¸°í™” í•˜ê³  deActiveFunction ì„ ì‹¤í–‰í•¨.
   * @param {boolean} value 
   */
  set isIntersecting(value){
    if( value !== this._isIntersecting ){
      if( this.timer !== null ){
        window.clearTimeout(this.timer);
        this.timer = null;
      }
    }
    if( !value ){
      this.deActiveFunction && this.deActiveFunction(this.element);
    }
    this._isIntersecting = value;
  }
  
  /**
   * ì—˜ë¦¬ë¨¼íŠ¸ ê°œë³„ ê¸°ì¤€ê°’ ì„ ëŒ€ìž…í•œ ê°’ì„ ì°¸ê³ í•˜ì—¬ í™”ë©´ê¸°ì¤€ìœ¼ë¡œ ê°ì‹œë˜ëŠ” ì—˜ë¦¬ë©´íŠ¸ê°€ ìœ„ì¹˜í•˜ëŠ” Y ê°’ì˜ ë°±ë¶„ìœ¨
   * @returns {Number} 
   */
  get percentY(){
    const { element, datumPointY } = this;
    const rect = element.getBoundingClientRect();
    const pointY = (()=>{
      if( typeof datumPointY === 'string' ) {
        switch (datumPointY) {
          case 'top':
            return 0;
          case 'bottom':
            return rect.height;
          default:
            return rect.height / 2;
        }
      } else {
        return datumPointY;
      }
    })();
    const winHeight = window.innerHeight;
    const y = rect.y + pointY;
    const percentY = (winHeight - y) / winHeight * 100;
    return percentY;
  }

  /**
   * ì—˜ë¦¬ë¨¼íŠ¸ ê°œë³„ ê¸°ì¤€ê°’ ì„ ëŒ€ìž…í•œ ê°’ì„ ì°¸ê³ í•˜ì—¬ í™”ë©´ê¸°ì¤€ìœ¼ë¡œ ê°ì‹œë˜ëŠ” ì—˜ë¦¬ë©´íŠ¸ê°€ ìœ„ì¹˜í•˜ëŠ” X ê°’ì˜ ë°±ë¶„ìœ¨
   * @returns {Number} 
   */
  // TODO: ê°œë°œ í•„ìš”.
  get percentX(){
    const { element, datumPointX } = this;
    const rect = element.getBoundingClientRect();
    const pointX = (()=>{
      if( typeof datumPointX === 'string' ) {
        switch (datumPointX) {
          case 'left':
            return 0;
          case 'right':
            return rect.width;
          default:
            return rect.width / 2;
        }
      } else {
        return datumPointX;
      }
    })();
    const winWidth = window.innerWidth;
    const x = rect.x + pointX;
    const percentX = (winWidth - x) / winWidth * 100;
    return percentX;
  }

  /**
   * íƒ€ì´ë¨¸ì™€ ìƒê´€ì—†ì´ ì§„ìž…ì‹œ ì‹¤í–‰ë˜ëŠ” í•¨ìˆ˜.
   */
  direct(){
    if( this._isIntersecting ){
      this.directFunction && this.directFunction(this.element);
    }
  }

  /**
   * íƒ€ì´ë¨¸ë¥¼ í™œì„±í™” í•˜ê³ , í•¨ìˆ˜ ì‹¤í–‰ ì¤€ë¹„ ìƒíƒœë¡œ ë³€ê²½.
   * _isIntersecting ì´ true ì¸ ê²½ìš° ìµœì¢… activeFunction ë¥¼ ì‹¤í–‰.
   * @method
   */
  active(){
    const { activeTimer } = this;
    if( this.timer === null ){
      this.timer = window.setTimeout(()=>{
        if( this._isIntersecting ){
          this.activeFunction && this.activeFunction(this.element);
        }
      }, activeTimer);
      this.direct();
    }
  }

  /**
   * _isIntersecting ê°’ê³¼ ë¬´ê´€í•˜ê²Œ ê°•ì œë¡œ í•¨ìˆ˜ ì‹¤í–‰ì„ ì´ˆê¸°í•˜ê³ ,
   * deActiveFunction ì„ ì‹¤í–‰í•¨.
   * @method
   */
  deActive(){
    if( this.timer !== null ){
      window.clearTimeout(this.timer);
      this.timer = null;
    }
    this.deActiveFunction && this.deActiveFunction(this.element);
  }
}

/**
 * string, nodeList, element ë¥¼ ì²´í¬í•˜ì—¬, [element] ë¡œ ë¦¬í„´í•¨.
 * @param {String|HTMLElement|NodeList} elements 
 * @returns {Array} [... elements] 
 */
function elementsArray(elements){
  if( typeof elements === 'string' ){
    return Array.from(document.querySelectorAll(elements));
  } else {
    if( elements.length ) {
      return Array.from(elements);
    } else {
      if( elements ) return [elements]
    }
  }
}

/**
 * ElementScrollWatcher ì˜ ê¸°ë³¸ setting ê°’
 * @namespace defaultSetting
 * @property {Window|HTMLElement} root - ìŠ¤í¬ë¡¤ ì´ë²¤íŠ¸ê°€ ë°”ì¸ë“œ ë˜ëŠ” ì˜ì—­.
 * @property {Number} activePercentY   - ì§„ìž… ì²´í¬ ì‹œìž‘ í¬ì¸íŠ¸ 
 * @property {Number} deActivePercentY -  ì§„ìž… ì²´í¬ ì—”ë“œ í¬ì¸ë“œ
 * @property {Number} activePercentX
 * @property {Number} deActivePercentX
 * @property {Number} activeDelay      - ì§„ìž…ì‹œ í•´ë‹¹ ì‹œê°„ í›„ í•¨ìˆ˜ ì‹¤í–‰ë¨.
 * @property {Number} threshold        - intersectionObserve ì˜ threshold 
 * @property {null|function} direct    - ì§„ìž…í–ˆì„ë•Œ ëŒ€ê¸° ì—†ì´ ë°”ë¡œ ì‹¤í–‰ ë˜ëŠ” callback
 * @property {null|function} active    - ì§„ìž…í–ˆì„ë•Œ ì‹¤í–‰ë  callback 
 * @property {null|function} deActive  - ë‚˜ê°”ì„ë•Œ ì‹¤í–‰ë  callback
 * @property {null|function} scroll    - ìŠ¤í¬ë¡¤ ë ë•Œ ì‹¤í–‰ë  callback
 * @property {boolean} init            - ìµœì´ˆ init ì„ í• ì§€ ì˜µì…˜
 * @property {string|number} checkY    - top|middle|bottom|custom number(px),target ì˜ ê¸°ì¤€ì .
 * @property {string|number} checkX    - left|center|right|custom number(px)
 */
const defaultSetting = {
  root: null,
  activePercentY: 60,
  deActivePercentY : 90,
  activePercentX: 0,
  deActivePercentX : 100,
  activeDelay: 1000,
  threshold: 0.1,
  direct: null,
  active: null,
  deActive: null,
  scroll: null,
  init: true,
  checkY: 'top',
  checkX: 'left'
}

/**
 * ë©”ì¸ class.
 * ìƒˆë¡œìš´ ìŠ¤í¬ë¡¤ ê°ì‹œìžë¥¼ ìƒì„±í•œë‹¤.
 * @class
 */
export default class ElementScrollWatcher {
  /**
   * @constructor
   * @param {String|HTMLElement|HTMLCollection} elements String = selector ex) `.element` or `#id`
   * @param {Ojbect} setting
   * @param {Window|HTMLElement} setting.root - ìŠ¤í¬ë¡¤ ì´ë²¤íŠ¸ê°€ ë°”ì¸ë“œ ë˜ëŠ” ì˜ì—­.
   * @param {Number} setting.activePercentY   - ì§„ìž… ì²´í¬ ì‹œìž‘ í¬ì¸íŠ¸ 
   * @param {Number} setting.deActivePercentY -  ì§„ìž… ì²´í¬ ì—”ë“œ í¬ì¸ë“œ
   * @param {Number} setting.activePercentX
   * @param {Number} setting.deActivePercentX
   * @param {Number} setting.activeDelay     - ì§„ìž…ì‹œ í•´ë‹¹ ì‹œê°„ í›„ í•¨ìˆ˜ ì‹¤í–‰ë¨.
   * @param {Number} setting.threshold       - intersectionObserve ì˜ threshold 
   * @param {null|function} setting.direct   - ì§„ìž…í–ˆì„ë•Œ ëŒ€ê¸° ì—†ì´ ë°”ë¡œ ì‹¤í–‰ ë˜ëŠ” callback
   * @param {null|function} setting.active   - ì§„ìž…í–ˆì„ë•Œ ì‹¤í–‰ë  callback 
   * @param {null|function} setting.deActive - ë‚˜ê°”ì„ë•Œ ì‹¤í–‰ë  callback
   * @param {null|function} setting.scroll   - ìŠ¤í¬ë¡¤ ë ë•Œ ì‹¤í–‰ë  callback
   * @param {boolean} setting.init           - ìµœì´ˆ init ì„ í• ì§€ ì˜µì…˜
   * @param {string|number} setting.checkY   - top|middle|bottom|custom number(px),target ì˜ ê¸°ì¤€ì .
   * @param {string|number} setting.checkX   - left|center|right|custom number(px)
   */
  constructor(elements, setting={}){
    // set
    const option = Object.assign({}, defaultSetting, setting);;
    option.root = option.root === null ? window : option.root
    const items = elementsArray(elements);
    const checkItems = [];
    const eswObject = {};
    const XY = checkTargetPosXY(option.checkX, option.checkY);
    const checkY = XY.y;
    const checkX = XY.x;
    const io = new IntersectionObserver(entries=>{ // IO ë¥¼ ì§€ì •.
      entries.forEach(entry => {
        const target = entry.target;
        const item = this.getEswObj(target);
        item.esw.isIntersecting = entry.isIntersecting; // esw item ì˜ intersecting ê°’ì„ ì…‹íŒ… í•œë‹¤.

        // í™”ë©´ì— ë“¤ì–´ì˜¤ëŠ” element ëŠ” checkItems ì— ë„£ê³ ,
        // í™”ë©´ì—ì„œ ë‚˜ê°„ element ëŠ” checkItems ì—ì„œ ì œê±°í•¨.
        // ìŠ¤í¬ë¡¤ì‹œ loop ê°€ ê³„ì† ëŒê²Œ ë˜ê¸° ë•Œë¬¸ì— ì„±ëŠ¥ì„ ê³ ë ¤í•˜ì—¬...
        if( entry.isIntersecting ){
          this.checkItems.push(target);
        } else {
          const index = this.checkItems.indexOf( target );
          if( index >= 0 ){
            this.checkItems.splice(index, 1);
          }
        }
      });
    });
    
    Object.assign(this, {
      option,
      items,
      io,
      checkItems,
      checkY,
      checkX,
      eswObject,
      isInit : false,
      isDisable : false, // ë¹„í™œì„±í™” ìœ ë¬´
      boundMot: null // scroll event ë¥¼ ì €ìž¥í•¨.
    });
    option.init && this.init();
  }

  /**
   * element ì˜ dataset.eswId ê°’ì„ ì²´í¬í•˜ì—¬ object ë¥¼ ë¦¬í„´í•¨.
   * @param {element} element 
   * @returns {Object} HTMLElement, id, esw object
   */
  getEswObj(element){
    const id = element.dataset.eswId;
    const esw = this.eswObject[id];
    return {element, id, esw};
  }

  // ìŠ¤í¬ë¡¤ì‹œ ì‹¤í–‰í•¨.
  mot(){
    if( this.isDisable ) return false;
    const { checkItems , option } = this;
    checkItems.forEach(element => {
      const item = this.getEswObj(element);
      const itemYPercent = item.esw.percentY;
      const itemXPercent = item.esw.percentX;
      const isIntersecting = item.esw._isIntersecting;

      
      const isActiveY = option.activePercentY < itemYPercent;
      const isDeActiveY = option.deActivePercentY < itemYPercent;
      const isActiveX = option.activePercentX < itemXPercent;
      const isDeActiveX = option.deActivePercentX < itemXPercent;

      if( isActiveY && isActiveX ){
        if( isDeActiveY || isDeActiveX ) {
          item.esw.deActive();
        } else {
          item.esw.active();
        }
      } else {
        item.esw.deActive();
      }

      /**
       * @namespace percent
       * @property {Number} x - xì¶•ìœ¼ë¡œ ì´ë™ëœ ì–‘(ë°±ë¶„ìœ¨)
       * @property {Number} y - yì¶•ìœ¼ë¡œ ì´ë™ëœ ì–‘(ë°±ë¶„ìœ¨)
       */
      const percent = {
        x : itemXPercent, 
        y : itemYPercent,
      }
      option.scroll && option.scroll(element, percent, isIntersecting);
    });
  }

  init(){
    const { items, io, option, mot } = this;
    this.boundMot = mot.bind(this);
    if( !this.isInit ) {
      option.root.addEventListener('scroll', this.boundMot, false);
    }

    items.forEach(element => {
      if( !element.dataset.eswInit ){
        const esw = new EswItem(element, this);
        this.eswObject[esw.id] = esw;
        io.observe(element, option.threshold);
        element.dataset.eswInit = 'init'
      }
    })
    this.isInit = true;
    window.addEventListener('load', ()=>{
      this.mot();  
    });
    this.mot();
  }

  /**
   * ë™ì ìœ¼ë¡œ element ê°€ ìƒì„± ëœ ê²½ìš° ì‚¬ìš©.
   * @param {HTMLCollection|HTMLElement|String} elements String = selector ex) `.element` or `#id`
   */
  update(elements){
    const addItems = elementsArray(elements);
    this.items = this.items.concat(addItems);
    this.init();
  }
  disable(){
    this.isDisable = true;
  }
  enable(){
    this.isDisable = false;
  }
  destroy(){
    const { items, io, option } = this;
    option.root.removeEventListener('scroll', this.boundMot, false);
    items.forEach(element => {
      io.unobserve(element);
      delete element.dataset.eswId;
      delete element.dataset.eswInit;
    });

    delete this.boundMot;
    delete this.io;
    delete this.items;
    delete this.checkItems;
    delete this.checkY;
    delete this.checkX;
    delete this.eswObject;
    delete this.isInit;
    delete this.isDisable;
    delete this.option;
    delete this;
  }
}